import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import * as moment from "moment-timezone";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { getDataNoTimeOut,} from "../../../../config/axios";
const HeadFormLaporanPenjualanTukar = lazy(() =>
  import("./HeadFormLaporanPenjualanTukar")
);


class FormLaporanPenjualanTukar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanDataPenjualan: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };
    // console.log(data_post);
    getDataNoTimeOut(`penjualan-tukar/reports?tanggal[gte]=${data_post.tgl_awal}&tanggal[lte]=${data_post.tgl_akhir}`)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          // console.log(res.data);
          ToastNotification("success", "Laporan Batal Terjual Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanDataPenjualan: res.data,
          });
        }
      })
      .catch((error) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false
        });
      });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">
            Laporan Barang Tukar
          </li>
        </ol>
        <h1 className="page-header">Laporan Barang Tukar </h1>
        <Panel>
          <PanelHeader>Laporan Barang Tukar</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadFormLaporanPenjualanTukar
             export={this.state.export}
             isLoading={this.state.isLoading}
             LaporanDataPenjualan={this.state.LaporanDataPenjualan}
             onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default FormLaporanPenjualanTukar;
